import React, { useEffect } from "react";

import { toast } from "react-toastify";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

import { listProducts } from "../../actions/productAction";
import Categories from "../../components/home/Categories";

import Reveal from "react-awesome-reveal";
import { keyframes } from "@emotion/react";
import ProductCard from "../../components/home/ProductCard";
import { BiStopwatch } from "react-icons/bi";
import SEO from "../../components/SEO";
const fadeInUp = keyframes`
  0% {
    opacity: 0;
    -webkit-transform: translateY(40px);
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
`;

const Watches = ({ match }) => {
  const keyword = match.params.keyword;
  const pageNumber = match.params.pageNumber || 1;

  const dispatch = useDispatch();
  const user = useSelector((state) => state.user);
  const { products, loading, error, page, pages } = useSelector(
    (state) => state.listProducts
  );
  useEffect(() => {
    dispatch(listProducts(keyword, pageNumber));
  }, [dispatch, keyword, pageNumber]);

  const watch = products.filter((x) => x.category === "watch");

  if (error) {
    toast.error(error);
  }
  return (
    <>
    <SEO title={"Watches"} />

      <section className="text-gray-600 body-font bg-gray-50">
        <div className="container px-5 pt-10 mx-auto">
          <div className="flex flex-col md:pr-10 md:mb-0 mb-6 pr-0 w-full md:w-auto md:text-left text-center ">
            <h2 className="text-xs text-indigo-500 tracking-widest font-medium title-font mb-1">
              01
            </h2>

            <h1 className="md:text-3xl  text-2xl font-medium title-font text-gray-700">
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                }}
              >
                Watches <BiStopwatch color="blue" />
              </div>
            </h1>
          </div>

          {/* list */}

          {loading ? (
            <p>loading...</p>
          ) : error ? (
            <h3> {toast.error(error)} </h3>
          ) : (
            <div className="flex flex-wrap -m-4 ">
              {watch.length === 0 ? (
                <p className="py-10 px-10">
                  Server is not found any watch for you :(
                </p>
              ) : (
                watch.map((item, index) => (
                  <div key={index} className="lg:w-1/4 md:w-1/2 p-4 w-full">
                    <Reveal
                      className="onStep"
                      keyframes={fadeInUp}
                      delay={300}
                      duration={600}
                      cascade
                    >
                      <ProductCard item={item} />
                    </Reveal>
                  </div>
                ))
              )}
            </div>
          )}
        </div>
      </section>

      <Reveal
        className="onStep"
        keyframes={fadeInUp}
        delay={300}
        duration={600}
        cascade
      >
        <Categories cats />
      </Reveal>
    </>
  );
};

export default Watches;
